.recruit{

&-ttl{
  @include fz(26);
  text-align: left;
  border-bottom: 1px solid black;
}
&-text{
  @include fz(16);
  margin-top: 20px;
}
&-table{
  margin-top: 20px;
  width: 100%;
  tr{
    
    th{
      @include fz(16);
      border: 1px solid black;
      text-align: center;
      padding: 10px;
      width: 30%;
      background-color: #f5f6f9;
    }
    td{
      @include fz(16);
      padding: 10px;
      border: 1px solid black;
      width: 70%;
    }
  }
}
._career{
  margin-top: 80px;
}

}